import React from "react";
import ReactDOM from "react-dom";
import "antd/dist/antd.css";
import "./index.css";
import { Tree, Button } from "antd";

function dig(path = "0", level = 2) {
  const list = [];
  const size = level === 0 ? 2000 : 10;
  for (let i = 0; i < size; i += 1) {
    const key = `${path}-${i}`;
    const treeNode = {
      title: key,
      key
    };

    if (level > 0) {
      treeNode.children = dig(key, level - 1);
    }

    list.push(treeNode);
  }
  return list;
}

const treeData = dig();

ReactDOM.render(
  <div>
    <Button
      onClick={() => {
        console.log(treeData);
      }}
    >
      onclick
    </Button>
    <Tree
      treeData={treeData}
      checkable
      onCheck={(checkedKeys) => {
        console.log(checkedKeys);
      }}
      height={300}
      defaultExpandAll
    />
  </div>,
  document.getElementById("container")
);
